export default {
	data(){
		return {
			addressData:[]
		}
	},
	onShow(){
		this.$request.GET({
			url:this.$api.apiUrl.GET_SHOW_ADDRESS
		}).then(res=>{
			// console.log(res);
			
			if(res.code===200){
				this.addressData = res.data
			}else{
				this.$api.msg(res.msg)
			}
		})
	},
	methods:{
		addAddress(){
			uni.navigateTo({url:"/pages/mys/addressForm/addressForm"})
		},
		update(e){
			uni.navigateTo({
				url:`/pages/mys/addressForm/addressForm?id=${e.id}`
			})
		},
		// 是否删除
		deleteAddress(e){
			uni.showModal({
				title: '消息提示',
				content: '确定删除该地址',
				success:(isBtn) => {
					if (isBtn.confirm) {
						this.$request.POST({
							url:this.$api.apiUrl.POST_ADDRESS_DELETE,
							data:{
								id:e.id
							}
						}).then(res=>{
							if(res.errcode===1){
								// 删除当前页面中的数据
								const result = this.addressData.findIndex(item=>{
									return item.id === e.id
								})
								this.addressData.splice(result,1)
							}
							this.$api.msg(res.errmsg)
						})
					}if (isBtn.cancel) {
						return false;
					}
				}
			})
		},
		setDefault(e){
			this.$request.POST({
				url:this.$api.apiUrl.POST_SET_DEFAULT,
				data:{
					id:e.id
				}
			}).then(res=>{
				if(res.errcode === 1){
					for (let i = 0; i < this.addressData.length; i++) {
						const element = this.addressData[i];
						if(element.id===e.id){
							element.is_default = 1;
						}else{
							element.is_default = 0;
						}
					}
				}
				this.$api.msg(res.errmsg)
			})
		}
	},
	computed:{
	}
}
